import React, { useEffect, useState } from 'react'

import { fetchRoomDetail, fetchNewMessage, fetchRoomAction, timeSince } from '../utils'

import { Box, Textarea, Text, Button, Input, useToast } from '@chakra-ui/react'

import '../room.css';



const RoomDetailPage = ({ dataset }) => {

  const toast = useToast()
  const { path, username } = dataset
  let roomId = path?.split('-')[1]
  if (!roomId?.includes('/')) {
    roomId = roomId + '/'
  }
  const [room, setRoom] = useState(null)
  const [message, setMessage] = useState('')
  const [roomMessages, setRoomMessages] = useState([])
  const [isMember, setIsMember] = useState(null)
  const [participants, setParticipants] = useState([])

  useEffect(() => {
    fetchRoomDetail(roomId, (response, status) => {
      if (status === 200) {
        setRoom(response)
        setRoomMessages(response.messages)
        setIsMember(response.is_member)
        setParticipants(response.participants)
      } // else {
        // alert('Error finding room.')
      // }
    })
  }, [])

  const handleSubmitNewMessage = () => {
    if (room !== null && message !== '') {
      fetchNewMessage(roomId, message, (response, status) => {
        if (status === 201) {
          let newMessages = [ ...roomMessages ]
          newMessages.unshift(response)
          setRoomMessages(newMessages)
          setMessage('')
        }
      })
    } else {
      if (!toast.isActive('info')) {
        toast({
          id: 'info',
          title: 'Info.',
          description: "Your message cannot be empty.",
          status: 'info',
          duration: 1500,
          isClosable: true,
        })
      }
    }
  }

return (
  <>
    <Button onClick={(e) => {
        e.preventDefault()
        window.location.href = '/'
    }} m='2' colorScheme='purple'>Back</Button>
  <div>

  

    <div align='center' style={{ width: '70%', float: 'left'}}>
      <Box style={{border: '2px solid #60039e', borderRadius: '15px', background: '#e3acf2'}} w='65%' h='90%' m='10'>
        <Box align='left' pl='5'>
          <Text fontWeight='bold' as='samp' fontSize='3xl'>{room?.name}</Text><br />
          <Text as='cite'>{room?.description}</Text><br /><br />
          <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white'>
              {room?.admin?.username?.[0]} 
          </span>Host @{room?.admin?.username}
        </Box>
        <Box align='right'>
          <Button m='4' colorScheme='purple' onClick={(e) => {
            e.preventDefault()
            fetchRoomAction(roomId.includes('/') ? roomId.substring(0, roomId.length - 1) : roomId, isMember ? 'leave' : 'join', (response, status) => {

              status === 200 && fetchRoomDetail(roomId, (response, status) => {
                  if (status === 200) setParticipants(response.participants)
                })
                
              if (status === 200 && !isMember) {

                if (!toast.isActive('join')) {
                  toast({
                    id: 'join',
                    title: 'Success',
                    description: `You've successfully joined '${room?.name}'.`,
                    status: 'success',
                    duration: 2000,
                    isClosable: true,
                  })
                }
              }
            })
            setIsMember(!isMember)
          }}>
            {isMember ? 'Leave' : 'Join'}
          </Button>
        </Box>

        <hr width='90%'/>

        <Box style={{ overflowY: 'scroll', height: '400px', width: '600px', borderRadius: '15px', background: '#f1c7fc'}}
          m='10' p='5'>

            {isMember && <form onSubmit={(e) => {
                  e.preventDefault()
                  handleSubmitNewMessage()
              }}>
                <Input focusBorderColor='purple.300' onChange={e => setMessage(e.target.value)} value={message} placeholder='Say something...' />
              </form>}

              {roomMessages?.slice(0, 250).map(({ user, body, created_at }) => (
                <>
                  <Box style={{ border: user === username ? '2px solid #60039e' : '1px solid #60039e', borderRadius: '25px'}} m='4' p='2'>
                      <Box mb='2'></Box>

                      <Box _hover={{ cursor: 'pointer' }} onClick={e => {
                          e.preventDefault()
                          window.location.href = `/profile-${user}`
                        }}>
                        <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white' style={{marginBottom: '50px'}}>
                          {user[0]}
                        </span>
                        <Text fontSize='sm' mt='2'>@{user} {user === username && '(me)'} {timeSince(new Date(created_at))} ago</Text>
                      </Box>

                      <Text fontSize='md'>„{body}”</Text>
                  </Box>
                </>
              ))}
              {!roomMessages.length && <Text as='sub'>No messages yet. Be the first one to say something!</Text>}
          </Box>
      </Box>
    </div>

    <div align='center' style={{ width: '30%', float: 'right'}}> 
      <Box style={{border: '2px solid #60039e', borderRadius: '15px', background: '#e3acf2'}} w='65%' h='90%' m='10'>
        <Text fontWeight='bold' as='samp' fontSize='3xl'>Participants</Text>
        <Text fontSize='sm'>({participants.length} joined)</Text>
        <Box style={{ overflowY: 'scroll', height: '600px', width: '200px', borderRadius: '15px', background: '#f1c7fc'}}
            m='10' p='5'>
              {participants.map(participant => (
                <>
                  <Box align='left' _hover={{ cursor: 'pointer' }} onClick={e => {
                    e.preventDefault()
                    window.location.href = `/profile-${participant.username}`
                  }}>
                    <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white' style={{marginBottom: '50px'}}>
                      {participant.username[0]}
                    </span>
                    {participant.username} {participant.username === username && '(me)'}
                  </Box><br />
                </>
              ))}
          </Box>   
      </Box>
    </div>
  </div>
                </>
)
}




export default RoomDetailPage