import React, { useState } from 'react'
import { Box, Text, IconButton, AlertDialog, AlertDialogOverlay, AlertDialogContent, 
  AlertDialogHeader, AlertDialogBody, AlertDialogFooter, Button } from '@chakra-ui/react'
import { BsBoxArrowRight } from 'react-icons/bs'
import { fetchRoomAction } from '../utils'


const RoomDisplayX = ({ room, member }) => {

  const [showAlert, setShowAlert] = useState(false)
  const room_ = { ...room }
  const [member_, setMember_] = useState(member)

  if ((!member_ && member) || room.is_member || (room.name === 'No Rooms found')) return null

  const numOfParticipants = room.participants?.length || room.participants_count || 0
  const displayParticipants = `${numOfParticipants} ${numOfParticipants === 1 ? 'Participant' : 'Participants'}`

  return <Box bg='purple.200' height='170px' borderRadius='25px' m='5'
              p='5' border='2px solid #60039e' _hover={{  shadow: '2px 2px 4px #999999', cursor: 'pointer' }} onClick={(e) => {
                e.preventDefault()
                window.location.href = `/room-${room.id}`
              }}>

                <AlertDialog
                    isOpen={showAlert}
                    onClose={() => setShowAlert(false)}>
                    <AlertDialogOverlay>
                      <AlertDialogContent>
                        <AlertDialogHeader fontSize='lg' fontWeight='bold'>
                          Leave Room
                        </AlertDialogHeader>

                        <AlertDialogBody>
                          Are you sure you want to leave {room_.name}?
                        </AlertDialogBody>

                        <AlertDialogFooter>
                          <Button onClick={() => setShowAlert(false)}>
                            Cancel
                          </Button>
                          <Button colorScheme='red' onClick={() => {
                            setShowAlert(false)
                            setMember_(false)
                            fetchRoomAction(room_.id, 'leave', (response, status) => {
                              if (status !== 200) {
                                alert('Error leaving the room.')
                              }
                            })

                          }} ml={3}>
                            Leave
                          </Button>
                        </AlertDialogFooter>
                      </AlertDialogContent>
                    </AlertDialogOverlay>
                  </AlertDialog>
                  

                {/* {room_.admin && <img src={room.admin.profile_pic} alt='debil' width='40' />} */}
                <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white'>
                    {room_?.admin?.username?.[0] || room_?.admin_username?.[0]} 
                </span>Host @{room_?.admin?.username || room_?.admin_username}

                {member_ === true && <IconButton
                  variant='outline'
                  icon={<BsBoxArrowRight />}
                  onClick={() => setShowAlert(true)}
                  colorScheme='purple'
                  ml='3'
                />}

                <Text fontSize='xs' mt={!member_ && 2}>{displayParticipants}</Text>
                
                <Text mt='2' fontSize='xl' fontWeight='bold' as='samp'>{room_.name}</Text><br />
                <Text fontSize='sm' as='i'>{room_.description?.length <= 75 ? room_.description : room_.description && `${room_.description?.substring(0, 75)}...`}</Text>
        </Box>
}



const RoomDisplay = ({ room, member }) => {

  const [showAlert, setShowAlert] = useState(false)
  const room_ = { ...room }
  const [member_, setMember_] = useState(member)

  if ((!member_ && member) || room.is_member || (room.name === 'No Rooms found')) return null

  const numOfParticipants = room.participants?.length || room.participants_count || 0
  const displayParticipants = `${numOfParticipants} ${numOfParticipants === 1 ? 'Participant' : 'Participants'}`

  return <Box bg='purple.200' height='170px' borderRadius='25px' m='5'
              p='5' border='2px solid #60039e' _hover={{  shadow: '2px 2px 4px #999999' }}>

                <AlertDialog
                    isOpen={showAlert}
                    onClose={() => setShowAlert(false)}>
                    <AlertDialogOverlay>
                      <AlertDialogContent>
                        <AlertDialogHeader fontSize='lg' fontWeight='bold'>
                          Leave Room
                        </AlertDialogHeader>

                        <AlertDialogBody>
                          Are you sure you want to leave {room_.name}?
                        </AlertDialogBody>

                        <AlertDialogFooter>
                          <Button onClick={() => setShowAlert(false)}>
                            Cancel
                          </Button>
                          <Button colorScheme='red' onClick={() => {
                            setShowAlert(false)
                            setMember_(false)
                            fetchRoomAction(room_.id, 'leave', (response, status) => {
                              if (status !== 200) {
                                alert('Error leaving the room.')
                              }
                            })

                          }} ml={3}>
                            Leave
                          </Button>
                        </AlertDialogFooter>
                      </AlertDialogContent>
                    </AlertDialogOverlay>
                  </AlertDialog>
                
                <Box _hover={{ cursor: 'pointer' }} onClick={e => {
                  e.preventDefault()
                  window.location.href = `/profile-${room.admin_username}`
                }}>
                  <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white'>
                      {room_?.admin?.username?.[0] || room_?.admin_username?.[0]} 
                  </span>Host @{room_?.admin?.username || room_?.admin_username}
                </Box>
                
                {member_ === true && <IconButton
                  variant='outline'
                  icon={<BsBoxArrowRight />}
                  onClick={() => setShowAlert(true)}
                  colorScheme='purple'
                  ml='3'
                />}

                <Text fontSize='xs' mt={!member_ && 2}>{displayParticipants}</Text>
                
                <Text mt='2' fontSize='xl' fontWeight='bold' as='samp'_hover={{ cursor: 'pointer' }} onClick={(e) => {
                e.preventDefault()
                window.location.href = `/room-${room.id}`
              }}>{room_.name}</Text><br />
                <Text fontSize='sm' as='i'>{room_.description?.length <= 75 ? room_.description : room_.description && `${room_.description?.substring(0, 75)}...`}</Text>
        </Box>
}


export default RoomDisplay