import React from 'react'
import { Box, Text, IconButton } from '@chakra-ui/react'


const MessageDisplay = ({ message : { body, room_name }, username, profile_pic }) => {
  return <Box bg='purple.200' height='200px' borderRadius='55px' m='5' p='7' border='2px solid #60039e'>
                  
                <span className='mx-1 px-3 py-2 rounded-circle bg-dark text-white'>
                    {username[0]} 
                </span>@{username} said in '{room_name}'

                
                <Box border='2px solid #60039e' bg='purple.400' borderRadius='5px' mt='4' p='2'>
                    <Text>{body.length > 200 ? `${body.substring(0, 200)}...` : body}</Text>
                </Box>

        </Box>
}

export default MessageDisplay