import React, { useState, useEffect } from 'react'
import { Box, Input, UnorderedList, ListItem, Heading, Text, SimpleGrid } from '@chakra-ui/react'

import { fetchProfileDetails, fetchSearchRooms } from '../utils'

import RoomsList from '../components/RoomsList'



const HomePage = ({ dataset }) => {

    const { username } = dataset

    const [myRooms, setMyRooms] = useState([])
    const [searchRooms, setSearchRooms] = useState([])
    const [searchQuery, setSearchQuery] = useState('')

    const handleSubmitSearchRooms = () => {
        fetchSearchRooms(searchQuery, (response, status) => {
            if (status === 200) {
                setSearchRooms(response.length ? response : 'No Rooms found')
            } else {
                alert('There was an error searching for rooms')
            }
        })    
    }

    useEffect(() => {
        fetchProfileDetails(username, (response, status) => {
            if (status === 200) {
                setMyRooms(response.member_rooms)
            } else {
                alert('There was an error finding your rooms')
            } 
        })
    }, [])

  return (
      <Box>
        <div style={{ width: '40%', float: 'left'}} align='center'>
            <Box m='4'>
                <form onSubmit={(e) => {
                    e.preventDefault()
                    handleSubmitSearchRooms()
                }}>
                    <Input placeholder='Search for Rooms' w='50%' value={searchQuery} onChange={e => setSearchQuery(e.target.value)} focusBorderColor='purple.400' />
                </form>
            </Box>

            <hr width='75%' />
            <RoomsList rooms={searchRooms} member={false} isSearch={true} searchQuery={searchQuery} />
        </div>

        <div style={{ width: '40%', float: 'right'}} align='center'>
            <Text as='samp' fontWeight='bold' fontSize='xl'>My Rooms</Text>
            <hr width='75%' />
            {myRooms.length ? <RoomsList rooms={myRooms} member isSearch={false} /> : <Text as='i'>You are not in any room :'(</Text>}
        </div>
      </Box>
  )
}

export default HomePage
