import React, { useState, useEffect } from 'react'
import { fetchProfileDetails, fetchAddFriend } from '../utils'
import { Image, Box, Text, Button, useToast } from '@chakra-ui/react' 
import RoomsList from '../components/RoomsList'
import MessageList from '../components/MessageList'

const ProfileDetailPage = ({ dataset }) => {

    const toast = useToast()
    let { path } = dataset
    const { me } = dataset 

    if (path?.endsWith('/')) {
      path = path.substring(0, path.length -  1)
    }
    const username = path?.split('-')[1]

    const [profileDetails, setProfileDetails] = useState(null)
    const [isFriend, setIsFriend] = useState(false)


    useEffect(() => {
      fetchProfileDetails(username, (response, status) => {
        if (status !== 404) {
          setProfileDetails(response)
          setIsFriend(response.is_friend)
        }
      })
    }, [])

    
  if (profileDetails === null) return <div>
    <h1>User not found!</h1>
  </div>

  return (
    <>
    <Button onClick={(e) => {
            e.preventDefault()
            window.location.href = '/'
        }} m='2' colorScheme='purple'>Back</Button>
    <div align='center'>
      <Box style={{border: '2px solid #60039e', borderRadius: '20px', background: '#e3acf2'}} w='75%' h='90%' m='10'>
        <Box mt='5'>
          <Image src={profileDetails.profile_pic} alt='pfp' borderRadius='full'
          boxSize='150px' style={{border: '1px solid'}}/>
          <Text fontSize='xl' fontWeight='bold'>{profileDetails.first_name} {profileDetails.last_name}</Text>
          <Text fontSize='xl' color='blue'>@{profileDetails.username}</Text>
        </Box>

        {username !== me ?
          <Button mt='3' colorScheme='purple' onClick={e => {
            e.preventDefault()
            fetchAddFriend(isFriend ? 'remove' : 'add', username, (response, status) => {
              if (status === 200) {
                if (!toast.isActive('befriend') && !isFriend) {
                  toast({
                    id: 'befriend',
                    title: 'Success',
                    description: `Added ${username} to Friends.`,
                    status: 'success',
                    duration: 2000,
                    isClosable: true,
                  })
                }
                setIsFriend(!isFriend)
              }
            })
          }}>{isFriend ? 'Remove Friend' : 'Add Friend'}</Button> : <Button mt='3' colorScheme='purple' onClick={e => {
            e.preventDefault()
            window.location.href = '/profile/edit'
          }}>Edit Profile</Button>}
        

        <Text align='left' ml='5' color='purple' fontWeight='bold' fontSize='xl'>LOCOATION</Text>
        <Text align='left' ml='5' fontSize='lg'>{profileDetails.location || 'No location Provided.'}</Text>

        <Text align='left' ml='5' mt='7' color='purple' fontWeight='bold' fontSize='xl'>BIO</Text>
        <Text align='left' ml='5' mb='5'>{profileDetails.bio || 'No BIO Provided.'}</Text>
        
        <div>
          <Box align='left' style={{ float: 'left', width: '50%'}}>
            <Text align='left' ml='5' color='purple' fontWeight='bold' fontSize='xl' mt='7'>ROOMS {username.toUpperCase()} PARTICIPATES IN</Text>
            {profileDetails.member_rooms.length ? <RoomsList rooms={profileDetails.member_rooms} w='80%' profile /> : <Text align='left' ml='5'>{username} does not participate in any room</Text>}
          </Box>

          <Box align='left' style={{ float: 'right', width: '50%'}}>
            <Text align='left' ml='5' color='purple' fontWeight='bold' fontSize='xl' mt='7'>RECENT ACTIVITIES</Text>
            {profileDetails.messages.length ? <MessageList messages={profileDetails.messages} username={username} profile_pic={profileDetails.profile_pic} /> : <Text align='left' ml='5'>{username} has no recent messages</Text>}
          </Box>
        </div>
        
      </Box>
    </div>
          </>
  )
}


